@import '_service';

/* General */

// Typography
body {
    margin: 0;
    padding: 0;

    color: $gray-base;
    font: normal 400 var(--text-size)/1.5 $font-family-body;
}

* {
    box-sizing: border-box;
}

h1,
.h1 {
    margin: 0 0 60px 0;

    color: $black;
    font: normal 900 var(--h1-size)/1.2 $font-family-title;
}

h2,
.h2 {
    margin: 60px 0 50px 0;

    color: $black;
    font: normal 900 var(--h2-size)/1.2 $font-family-title;
}

h3,
.h3 {
    margin: 50px 0 30px 0;

    color: $black;
    font: normal 900 var(--h3-size)/1.3 $font-family-title;
}

h4,
.h4 {
    margin: 30px 0;

    color: $black;
    font: normal 700 var(--h4-size)/1.3 $font-family-title;
}

h5,
.h5 {
    color: $black;
    font: normal 700 var(--h5-size)/1.2 $font-family-title;
}

h6,
.h6 {
    color: $black;
    font: normal 700 var(--text-size)/1.4 $font-family-title;
}

img {
    display: block;
    max-width: 100%;
}

p {
    margin: 0 0 20px 0;

    &:last-child {
        margin-bottom: 0;
    }
}

ul,
ol {
    margin: var(--indent-xs) 0 var(--indent-m) 0;
    padding: 0;
    list-style: none;
}

ul li,
ol li {
    position: relative;
}

ul {
    margin-left: var(--indent-xs);
}

ul li {
    padding-left: calc(var(--indent-3xs) + 4px);

    &:before {
        @include pseudo($width: 4px, $height: 4px, $top: 0.6em);
        border-radius: 50%;
        background-color: $accent-color;
    }
}

ul li + li {
    margin-top: 8px;
}

ol {
    counter-reset: ordered-list;
}

ol li {
    padding-left: calc(var(--indent-3xs) + 1em);
    
    &:before {
        content: counter(ordered-list) '.';
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        counter-increment: ordered-list;
    }
}

ol li + li {
    margin-top: 13px;
}

.ol-large li {
    &:before {
        color: $black;
    }
}

.ol-large__title {
    margin-bottom: 6px;
    color: $black;
    font-weight: 700;
    line-height: 1.4;
}

a {
    color: $black;
    text-decoration: none;
    font-weight: 700;

    transition: 0.3s;

    &:hover {
        color: $accent-color
    }
}

.link {
    color: $black;
    text-decoration: none;
    font-weight: 700;
    
    transition: 0.3s;
    
    &:hover {
        color: $accent-color;
    }

    &_accent {
        color: $accent-color;
        
        &:hover {
            color: $black;
        }
    }
}

table {
    width: 100%;
    margin: var(--indent-s) 0 var(--indent-l) 0;
    border-collapse: collapse;
}

thead {
    background-color: $black;
    color: $white;
}

tr {
    &:nth-child(even) {
        background-color: $light-bg;
    }
}

th, 
td {
    padding: var(--indent-3xs) var(--indent-xs);
    text-align: left;
}

// containers
.container {
    width: 100%;
    max-width: var(--container);
    margin: auto;
}

.wrapper {
    width: 100%;
    max-width: var(--wrapper);
}

.inner-container {
    width: 100%;
    max-width: var(--inner-container);
}

// Universal elements
.mb-4xl {
    margin-bottom: var(--indent-4xl);
}

.mb-3xl {
    margin-bottom: var(--indent-3xl);
}

.mb-2xl {
    margin-bottom: var(--indent-2xl);
}

.mb-xl {
    margin-bottom: var(--indent-xl);
}

.mb-l {
    margin-bottom: var(--indent-l);
}

.mb-m {
    margin-bottom: var(--indent-m);
}

.mb-s {
    margin-bottom: var(--indent-s);
}

.mb-xs {
    margin-bottom: var(--indent-xs);
}

.mb-2xs {
    margin-bottom: var(--indent-2xs);
}

.subtitle {
    margin-bottom: 50px;

    font-size: var(--subtitle-size);
}

.double-block {
    column-count: 2;
    column-gap: var(--indent-3xl);

    > * {
        display: inline-block;
    }
}

.info-block {
    position: relative;
    display: inline-block;
    padding: 0 0 5px 40px;
    
    color: $black;
    font-size: 20px;

    &:before {
        @include pseudo($width: 10px);
        margin-right: 30px;
        background-color: $accent-color;
    }
}

.tile {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
}

.tile__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px;
    border-radius: $border-radius;
    background-color: $white;

    &:nth-child(6n + 1),
    &:nth-child(6n + 6) {
        grid-column: span 2;

        .tile__item-image {
            align-self: flex-end;
        }
    }
}

.tile__item-name {
    margin-bottom: 30px;
    color: inherit;
}

// Buttons
.btn {
    position: relative;
    display: block;
    width: fit-content;
    padding: 20px 40px;
    overflow: hidden;

    border: 1px solid $accent-color;
    border-radius: $border-radius;

    text-align: center;
    color: $white;
    font-weight: 500;
    line-height: 1.2;

    cursor: pointer;
    transition: {
        property: background-color, color;
        duration: 0.3s;
    }

    &:before {
        @include pseudo();
        background-color: $accent-color;
        z-index: -1;
        transition: 0.3s ease;
    }

    &:hover {
        color: $accent-color;
        
        &:before {
            right: 0;
            left: auto;
            width: 0;
        }
    }

    &_light {
        color: $accent-color;

        &:before {
            right: 0;
            left: auto;
            width: 0;
            background-color: $accent-color;
        }

        &:hover {
            color: $white;

            &:before {
                right: auto;
                left: 0;
                width: 100%;
            }
        }
    }

    &_medium {
        padding: {
            right: 92px;
            left: 92px;
        }
    }
}

// Forms 
.form {
    padding: var(--indent-2xl) var(--indent-3xl);
    border-radius: $border-radius;
    background-color: $light-bg;
    text-align: center;
}

.form__title {

}

.form__subtitle {
    max-width: var(--inner-container);
    margin: auto;
}

// Cards
.cards {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
}

.cards-item__image {
    position: relative;
    padding-top: 100%;
    border-radius: $border-radius;
    background-color: $white;
}

.cards-item__image-inner {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 12px;
}

.cards-item__title {
    margin-top: var(--indent-xs);
    color: inherit;
}

.cards-item__text {
    margin-top: var(--indent-xs);
}

// Persons
.persons {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: var(--indent-3xl);
}

.persons-item {
    display: flex;
}

.persons-item__image {
    position: relative;
    width: 140px;
    padding: 8px 0 0 8px;
    margin-right: var(--indent-s);

    &:before {
        @include pseudo($width: 70px, $height: 70px);
        border-radius: $border-radius;
        background-color: $accent-color;
        z-index: -1;
    }
}

.persons-item__image-inner {
    border-radius: $border-radius;
    overflow: hidden;
}

.persons-item__info {
    display: flex;
    flex-direction: column;
}

.persons-item__name {
    color: $black;
    font-size: 20px;
    font-weight: 700;
}

.persons-item__position {
    margin-top: var(--indent-3xs);
}

.persons-item__contacts {
    margin-top: var(--indent-s);
}

.persons-item__email {
    margin-top: var(--indent-3xs);
}

/* General - end */



/* Home page */
.home-products {
    padding: var(--indent-2xl) 0;
    @include pattern-bg();
}

.home-products__title {
    margin-top: 0;
}
/* Home page - end */

