/* Mixins */
@mixin font-face($font-family, $path-name, $font-weight) {
    @font-face {
        font-family: $font-family;
        src: url('../fonts#{$path-name}.woff2') format('woff2'),
            url('../fonts#{$path-name}.woff') format('woff');
        font-weight: $font-weight;
        font-style: normal;
        font-display: swap;
    }
}

@mixin pseudo($width: 100%, $height: 100%, $top: 0, $left: 0) {
    content: '';
    position: absolute;
    top: $top;
    left: $left;
    display: block;
    width: $width;
    height: $height;
}

@mixin pattern-bg() {
    background: $light-bg url('../img/pattern.png') center/cover no-repeat;
}
/* Mixins - end */

/* Variables */
$font-family-body: 'Roboto', sans-serif;
$font-family-title: 'Montserrat', sans-serif;

$white: #fff;
$black: #212121; // Titles

$gray-base: #5e5e5e; // Base text
$gray: #9e9e9e; // Light text
$gray-light: #bdbdbd; // Lightest text
$gray-lightest: #f2f2f2; // Background and border ????? #e0e0e0

$light-bg: #f1f3f3;
$accent-color: #f50000;

$border-radius: 10px;

:root {
    --container: 1168px;
    --wrapper: 870px;
    --inner-container: 682px;

    --text-size: 16px;

    --h1-size: 62px;
    --h2-size: 46px;
    --h3-size: 34px;
    --h4-size: 32px;
    --h5-size: 24px;

    --subtitle-size: 24px;

    --indent-4xl: 130px;
    --indent-3xl: 108px;
    --indent-2xl: 70px;
    --indent-xl: 60px;
    --indent-l: 50px;
    --indent-m: 40px;
    --indent-s: 30px;
    --indent-xs: 20px;
    --indent-2xs: 12px;
    --indent-3xs: 10px;
}
/* Variables - end */

/* Fonts */
@include font-face('Roboto', 'Roboto-Regular', 400);
@include font-face('Roboto', 'Roboto-Medium', 500);
@include font-face('Roboto', 'Roboto-Bold', 700);
@include font-face('Montserrat', 'Montserrat-Bold', 700);
@include font-face('Montserrat', 'Montserrat-Regular', 400);
@include font-face('Montserrat', 'Montserrat-Black', 900);
/* Fonts - end */
